@media screen and (max-width: 456px) {
  input[type=submit] {
    margin-top: 10px !important;
  }
}
@font-face {
  font-family: font-5;
  src: url("../fonts/PragatiNarrow-Regular.eot") format("eot");
  src: url("..//fonts/PragatiNarrow-Regular.svg") format("svg");
  src: url("../fonts/PragatiNarrow-Regular.ttf") format("ttf");
  src: url("../fonts/PragatiNarrow-Regular.woff") format("woff");
  src: url("../fonts/PragatiNarrow-Regular.woff2") format("woff2");
}
@font-face {
  font-family: font-6;
  src: url("../fonts/Montserrat-Regular.svg") format("svg");
  src: url("../fonts/Montserrat-Regular.ttf") format("ttf");
  src: url("../fonts/Montserrat-Regular.woff") format("woff");
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
}
html {
  scroll-behavior: smooth;
}

body {
  overflow-y: hidden;
  overflow-x: hidden;
  background-repeat: no-repeat;
  transition: 0.15313s ease;
}

a {
  color: #000;
  text-decoration: none;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "font-6";
}

h1, h2, h3, h4, h5, h6 {
  font-family: "font-6";
  font-weight: bold;
}

::selection {
  background-color: #1e437a;
  color: white;
}

::-webkit-scrollbar {
  display: none;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
}

#age-calculator {
  background: linear-gradient(130deg, rgb(75, 34, 137), rgb(101, 171, 221));
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
  display: flex;
  justify-content: center;
  align-items: center;
}
#age-calculator::-webkit-scrollbar {
  display: none;
}
#age-calculator .container .age-calculator-inner .rectangle {
  width: 30px;
  position: relative;
  z-index: 2;
}
#age-calculator .container .age-calculator-inner .rectangle .circle {
  width: 20px;
  height: 20px;
  border-radius: 100%;
  background: linear-gradient(130deg, #cca5ff, rgba(204, 165, 255, 0));
  transition: 0.5313s ease;
  position: relative;
  left: -7px;
}
#age-calculator .container .age-calculator-inner .rectangle .line {
  position: relative;
  top: 6px;
  left: -1px;
  width: 7px;
  height: 270px;
  background: linear-gradient(to bottom, #cca5ff, rgba(204, 165, 255, 0.0352941176));
  border-radius: 5px;
}
#age-calculator .container .age-calculator-inner .content {
  width: calc(100% - 40px);
  background: linear-gradient(to bottom, #cca5ff, rgba(204, 165, 255, 0.1450980392));
  border-radius: 7px;
  padding: 15px 25px;
  position: relative;
  top: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#age-calculator .container .age-calculator-inner .content .title span {
  background: linear-gradient(90deg, #6625ff, #562cb7 35%, #5e2d96 50%, #562cb7 65%, #6625ff);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-position: 0 0;
  background-size: 200% 100%;
  animation: bgMove 3s infinite linear;
  user-select: none;
}
@keyframes bgMove {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 400% 0;
  }
}
#age-calculator .container .age-calculator-inner .content .calculate {
  background-color: rgba(255, 255, 255, 0.3490196078);
  padding: 15px 20px;
  border-radius: 8px;
  max-width: fit-content;
}
#age-calculator .container .age-calculator-inner .content .calculate form {
  display: flex;
  width: 100%;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}
#age-calculator .container .age-calculator-inner .content .calculate form input[type=date] {
  cursor: pointer;
  border-radius: 5px;
  padding: 7px;
  border: 0;
  color: #6c6c6c;
  outline: 0;
  background: #ffffff;
  font-size: 15px;
}
#age-calculator .container .age-calculator-inner .content .calculate form input[type=submit] {
  border-radius: 5px;
  padding: 7px 11px;
  border: 0;
  color: #ffffff;
  outline: 0;
  background: linear-gradient(to right, #736aa7, #af98ca);
  font-size: 15px;
  margin: 0 10px;
}
#age-calculator .container .age-calculator-inner .content .result {
  color: white;
}
#age-calculator .scroll-guide {
  width: 100%;
  bottom: 0;
  right: 0;
  left: 0;
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  visibility: visible;
  transition: 0.313s ease;
}
#age-calculator .scroll-guide .scroll-inner {
  width: 27px;
  height: 55px;
  border: 2px solid #fff;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7px;
}
#age-calculator .scroll-guide .scroll-inner .scroll-line {
  width: 7px;
  height: 7px;
  background-color: #fff;
  border-radius: 100%;
  position: absolute;
  top: 26%;
  animation: scroll 3s ease infinite;
}
@keyframes scroll {
  0% {
    top: 26%;
  }
  50% {
    top: 66%;
  }
  100% {
    top: 26%;
  }
}

#loading {
  width: 100%;
  height: 100vh;
  z-index: 50;
  position: fixed;
  opacity: 1;
  visibility: visible;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #4e4ede;
  transition: visibility 0.313s ease, opacity 0.313s ease;
}
#loading img {
  width: 80px;
}